<template>
  <div id="app">
    <div class="loading">
      <img src="./assets/img/loading.gif" alt="">
    </div>
    <Header v-if="childen"></Header>
    <transition name="fade">
        <router-view />
    </transition>
    <Footer v-if="footer"></Footer>
  </div>
</template>
<script>
import Footer from './components/footer';
import Header from './components/header';
export default {
  name: 'App',
  data(){
    return {
      childen: true,
      footer:true,
    }
  },
  mounted(){
    var _this = this;
    if(sessionStorage.lang){
        _this.apis.lang = sessionStorage.lang
        _this.$i18n.locale = sessionStorage.lang
    }else{
        _this.apis.lang = 'en'
        _this.$i18n.locale = 'en'
    }
  },
  watch:{
    $route(){
      var _this = this;
    }
  },
  components:{
    "Footer":Footer,
    "Header":Header,
  }
}
</script>

<style>
/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
} */
@font-face {
  font-family: syht;
  src: url(../static/SourceHanSansCN-Regular.otf);
}
html{
  font-family: syht;
}
#app{
  /* min-height: 100vh;
  background-image: url(./assets/img/mainBg.png);
  background-size: 100%; */
}
.loading{
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.2);
  position: fixed;
  top: 0;
  display: none;
  z-index: 9999;
}
.loading img{
  width: 2rem;
  height: 0.3rem;
  margin: 0 auto;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 1111;
}
.fade-enter-active, .fade-leave-avtive {
    transition: opacity .5s
}
.fade-enter, .fade-leave-to {
    opacity: 0
}
.el-select{
  width: 100%;
  height: 100%;
}
.el-input{
  width: 100%;
  height: 100%;
}
.el-input__inner{
  width: 100%;
  height: 100%;
}
.btn-prev,.el-pager li,.btn-next{
  background: none !important;
}
.sousuo1 .el-input__inner{
  width: 100%;
  height: 100%;
  background: none;
  border-right: none;
  color: #fff;
}
.sousuo1 .el-input__inner{
  border-radius: 0px;
  border: 2px solid #DCDFE6;
  border-right:none;
}
.sousuo1 .el-input-group__append{
  border-radius: 0px !important;
}
.sousuo1 .el-input-group__append{
  border: 2px solid #DCDFE6;
  border-left:none;
}
.sousuo1 .el-input__inner:hover{
  background: none;
}
.sousuo1 .el-input__inner:focus{
  border: 2px solid #DCDFE6;
  border-right: none;
}
.sousuo1 .el-input-group__append{
  background: none;
}
.sousuo2 .el-input__inner{
  border: none;
  background: #fff;
  border-radius: 0px;
}
.sousuo2 .el-input__inner:hover{
  border: none;
}
.sousuo2 .el-input__inner:focus{
  border: none;
  border-right: none;
}
.is-checked .el-radio__inner,.is-checked .el-checkbox__inner{
  background: #FFAD01 !important;
  border-color: #FFAD01 !important;
}
.is-checked .el-radio__label,.is-checked .el-checkbox__label{
  color: #606266 !important;
}
.is-active{
  /* color: #FFAD01 !important; */
}
.el-tabs__active-bar{
  background: #FF6D01 !important;
}
.el-tabs__item:hover{
  color: #FF6D01 !important;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
input[type=checkbox]{
     cursor: pointer;
     position: relative;
     width: 15px;
     height: 15px;
     font-size: 14px;
}
input[type=checkbox]::after{
     position: absolute;
     top: 0;
     background-color:  #fff;
     color: #000;
     width: 15px;
     height: 15px;
     display: inline-block;
     visibility: visible;
     padding-left: 0px;
     text-align: center;
     content: ' ';
     border: 1px solid grey;
}
.el-tabs__item.is-active{
  color: #FF6d01 !important;
}     
input[type=checkbox]:checked::after{
     /* content: ""; */
     font-size: 12px;
     font-weight: bold;
     background: #fff;
     border: 1px solid #FFAD01;
     background-image: url(./assets/img/shangcheng/jizhumima_02.png);
}
.el-collapse-item__header,.el-collapse-item__wrap{
  font-size: 16px;
  background: none;
}
.erji,.sanji{
  padding-left: 15px;
}
.fenye .number:hover{
  color: #FF6600 !important;
}
.fenye .active{
  color: #FF6600 !important;
}
.fenye .el-pagination button:hover{
  color: #FF6600 !important;
}
.shuliang .el-input__inner{
  border-radius: 0px;
}
.shuliang .el-input__inner:focus{
  border-color: #FF6600;
}
.shuliang .el-input-number__decrease:hover{
  color: #FF6600;
}
.shuliang .el-input-number__increase:hover{
  color: #FF6600;
}
.shuliang .el-input-number__decrease:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled){
  border-color: #FF6600;
}
.shuliang .el-input-number__increase:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled){
  border-color: #FF6600;
}
.xiangqing .el-tabs__item{
  font-size: 18px;
}
.el-tabs__nav-wrap::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #E4E7ED;
    z-index: 1;
}
.wodedingdan .el-tabs__nav-wrap::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #E4E7ED;
    z-index: 1;
    display: none;
}
.wodedingdan .el-tabs__active-bar{
  display: none;
}
.el-upload--picture-card, .el-upload{
  width: 124px; 
  height: 124px;
  line-height: 124px;
}
.el-upload--picture-card:hover, .el-upload:focus{
    border-color: #757575  !important;
    color: #757575 !important;
}
.el-rate__icon{
  font-size: 26px;
  margin-right: 0;
  color: #FF7200 !important;
}
</style>



