<template>
  <div :class="footer ? 'footer ' : 'footer hover'">.
    <!-- <div class="top" v-if="footer">
        <img src="../assets/img/footer.png" alt="">
    </div> -->
    <div class="bottom">
        <div class="title">
            {{$t('footer[0]')}}
        </div>
        <div class="nav">
            <div @click="tab(0)">{{$t('footer[1]')}}
            </div>
            <span></span>
            <div @click="tab(1)">{{$t('footer[2]')}}
            </div>
            <span></span>
            <div @click="tab(2)">{{$t('footer[3]')}}
            </div>
            <span></span>
            <div @click="tab(3)">{{$t('footer[4]')}}
            </div>
            <span></span>
            <div @click="tab(4)">{{$t('footer[5]')}}
            </div>
            <span></span>
            <div @click="tab(5)">{{$t('footer[6]')}}</div>
        </div>
        <div class="text">
            <p>{{info.address}}</p>
            <p>{{$t('info[0]')}} {{info.landline}}   {{info.phone}}    {{$t('info[1]')}}{{info.fax}}    {{$t('info[2]')}}{{info.email}}</p>
            <p class="bottom">{{info.beian}}</p>
        </div>
    </div>
    <div class="icon">
        <!-- <div>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
                <img src="../assets/img/shangcheng/tubiao_01.png" alt="">
            </el-tooltip>
        </div> -->
        <div>
            <a :href="info.YouTube_url" target="view_window">
                <img src="../assets/img/shangcheng/tubiao_01.png" alt="">
            </a>
        </div>
        <div>
            <a :href="info.Pinterest_url" target="view_window">
                <img src="../assets/img/shangcheng/tubiao_02.png" alt="">
            </a>
        </div>
        <div>
            <a :href="info.Facebook_url" target="view_window">
                <img src="../assets/img/shangcheng/tubiao_03.png" alt="">
            </a>
        </div>
        <div>
            <a :href="info.Instagram_url" target="view_window">
                <img src="../assets/img/shangcheng/tubiao_04.png" alt="">
            </a>
        </div>
    </div>
  </div>
</template>

<script>
    export default {
        beforeRouteEnter(to, from, next) {
            return next(vm => {});
        },
        data() {
            return {
                footerImg:'',
                footer:true,
                info:'',
                navA:['home','lianxi','lianxi','lianxi','lianxi','lianxi',],
            };
        },
        created:function(){
            
        },
        mounted:function(){
            var _this = this;
                _this.apis.get(_this.apis.config,{params:{lang:_this.apis.lang}}).then(function(res) {
                    if(res.data.code == 0){
                        _this.info = res.data.data
                    }
                })
        },
        watch:{
            $route(){
                var _this = this;
                if(_this.$route.name == 'home'){
                    _this.footer = true;
                }else{
                    _this.footer = false;
                }
            }
        },
        methods:{
                tab(index){
                    var _this = this;
                    _this.$router.push({name:_this.navA[index]});
                },
        },
        gorouter(url){
            var _this = this;
            window.location.href = url
        }
    }
</script>
<style scoped lang="less">
.footer{
    background: #333;
    padding-top: 35px;
    padding-bottom: 30px;
    .bottom{
        width: 1200px;
        margin: 0 auto;
        .title{
            text-align: center;
            font-size: 40px;
            color: #fff;
        }
        .nav{
            color: #fff;
            font-size: 18px;
            height: 86px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid #d2d2d2;
            margin-bottom: 38px;
            padding: 0 35px;
            &>div{
                position: relative;
                // span{
                //     display: inline-block;
                //     width: 1px;
                //     height: 15px;
                //     background: #fff;
                //     position: absolute;
                //     right: -15px;
                //     top: 50%;
                //     transform: translateY(-50%);
                // }
            }
            span{
                display: inline-block;
                width: 1px;
                height: 15px;
                background: #F9E9E4;
            }
        }
        .text{
            height: 120px;
            line-height: 25px;
            text-align: center;
            color: #fff;
            .bottom{
                color: #fff;
                margin-top: 30px;
            }
            p{
                color: #fff;
            }
        }
    }
    .icon{
        width: 200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
</style>


