import Vue from 'vue'
import Router from 'vue-router'

//
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
 return originalPush.call(this, location).catch(err => err)
}

Vue.use(Router)

export default new Router({
  routes: [
    {path: '*', redirect: '/shouye'},
    { path: '/shouye', title: "", name: 'home', component: () => import('@/pages/1shouye') },
    { path: '/proList',  name: 'proList', component: () => import('@/pages/2proList'),redirect: '/list',
      children:[
        { path: '/list', name: 'list', component: () => import('@/pages/childen/list1')},
        { path: '/xiangqing', name: 'xiangqing', component: () => import('@/pages/childen/xiangqing')},
        { path: '/dingdan', name: 'dingdan', component: () => import('@/pages/childen/dingdan')},
        { path: '/querendingdan', name: 'querendingdan', component: () => import('@/pages/childen/querendingdan')},
        { path: '/user', name: 'user', component: () => import('@/pages/childen/user'),redirect: 'xiugaimima',
          children:[
            { path: '/xiugaimima', name: 'xiugaimima', component: () => import('@/pages/childen/userVue/xiugaimima')},
            { path: '/wodedingdan', name: 'wodedingdan', component: () => import('@/pages/childen/userVue/wodedingdan')},
            { path: '/ziliao', name: 'ziliao', component: () => import('@/pages/childen/userVue/ziliao')},
            { path: '/bianjiziliao', name: 'bianjiziliao', component: () => import('@/pages/childen/userVue/bianjiziliao')},
            { path: '/dizhiguanli', name: 'dizhiguanli', component: () => import('@/pages/childen/userVue/dizhiguanli')},
            { path: '/shoucang', name: 'shoucang', component: () => import('@/pages/childen/userVue/shoucang')},
            { path: '/zhaohuo', name: 'zhaohuo', component: () => import('@/pages/childen/userVue/zhaohuo')},
            { path: '/gouwuche', name: 'gouwuche', component: () => import('@/pages/childen/userVue/gouwuche')},
          ]
        },
    ]},
    { path: '/guanyu', title: "", name: 'guanyu', component: () => import('@/pages/childen/guanyu'),redirect: '/zizhi',
      children:[
        { path: '/zizhi', name: 'zizhi', component: () => import('@/pages/childen/guanyu/zizhi')},
        { path: '/fazhan', name: 'fazhan', component: () => import('@/pages/childen/guanyu/fazhan')},
        { path: '/chanpin', name: 'chanpin', component: () => import('@/pages/childen/guanyu/chanpin')},
        { path: '/lianxi', name: 'lianxi', component: () => import('@/pages/childen/guanyu/lianxi')},
      ]
    },
    { path: '/xinwen', title: "", name: 'xinwen', component: () => import('@/pages/3xinwen')},
    { path: '/xinwenList', title: "", name: 'xinwenList', component: () => import('@/pages/4xinwenList') },
    { path: '/haiguan', title: "", name: 'haiguan', component: () => import('@/pages/5haiguan') },
    { path: '/pinglun', title: "", name: 'haiguan', component: () => import('@/pages/7pinglun') },
    { path: '/lianxi', title: "", name: 'lianxi', component: () => import('@/pages/6lianxi') },
    { path: '/bai', title: "bai", name: 'bai', component: () => import('@/pages/bai') },
  ]
})



