<template>
    <div class='header'>
        <div :class="topBackgroun ? 'top topWhite' : 'top topBlack'">
            <div class="nav">
                <div class="navFlex">
                    <div @click="$router.push({path:'/shouye'})">
                        {{$t('header.nav[0]')}}
                        <!-- <router-link to="/shouye"></router-link> -->
                        <span></span>
                    </div>
                    <div @click="$router.push({path:'/list',query:{title:$t('pro[3]')}})">
                        {{$t('header.nav[1]')}}
                        <!-- <router-link :to="{path:'/list',query:{title:''}}"></router-link> -->
                        <span></span>
                    </div>
                    <div @click="$router.push({path:'/haiguan'})">
                        {{$t('header.nav[2]')}}
                        <!-- <router-link to="/shouye"></router-link> -->
                        <span></span>
                    </div>
                    <div class="guanyu" @click="$router.push({path:'guanyu'})">
                        {{$t('header.nav[3]')}}
                        <!-- <router-link to="/shouye"></router-link> -->
                        <span></span>
                        <div class="erji">
                            <div @click="$router.push({path:'guanyu/zizhi'})">{{$t('header.nav[10]')}}</div>
                            <div @click="$router.push({path:'guanyu/chanpin'})">{{$t('header.nav[11]')}}</div>
                            <div @click="$router.push({path:'guanyu/fazhan'})">{{$t('header.nav[12]')}}</div>
                            <div @click="$router.push({path:'guanyu/lianxi'})">{{$t('header.nav[13]')}}</div>
                            <span></span>
                        </div>
                    </div>
                    <div>
                        <a :href="config.blog_url" target="view_window">
                            {{$t('header.nav[8]')}}
                        </a>
                        <!-- <router-link to="/shouye"></router-link> -->
                        <span></span>
                    </div>
                    <div @click="$router.push({path:'/pinglun'})">
                        {{$t('pingjia[0]')}}
                        <!-- <router-link to="/shouye"></router-link> -->
                        <span></span>
                    </div>
                </div>
                <div class="login" v-if="!login">
                    <div class="gouwuche" @click="gowuche()">
                        <div class="img">
                            <img src="../assets/img/shangcheng/gouwuche_02.png" alt="">
                        </div>
                        <div class="text">{{$t('header.nav[9]')}}</div>
                    </div>
                    <div class="denglu" @click="userClick(1)">{{$t('header.nav[4]')}}</div>
                    <div class="hr"></div>
                    <div class="zhuce" @click="userClick(2)">{{$t('header.nav[5]')}}</div>
                    <div class="yuyan">
                        <el-select v-model="value" :placeholder="$t('header.nav[7]')" :change="yuyan()">
                            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="login" v-else>
                    <div class="gouwuche" @click="gowuche()">
                        <div class="img">
                            <img src="../assets/img/shangcheng/gouwuche_02.png" alt="">
                        </div>
                        <div class="text">{{$t('header.nav[9]')}}</div>
                    </div>
                    <div class="denglu username" @click="$router.push({path:'ziliao',query:{title:''}})">{{userInfo.name}}</div>
                    <div class="hr"></div>
                    <div class="zhuce tuichu" @click="quit()">[{{$t('header.nav[6]')}}]</div>
                    <div class="yuyan">
                        <el-select v-model="value" :placeholder="$t('header.nav[7]')" :change="yuyan()">
                            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog class="loginDialog" :visible.sync="loginDialog" width="430px">
            <p class="loginTitle"><a @click="qiehuan(1)">{{$t('header.login[0]')}}</a></p>
            <div class="loginForm">
                <h1>{{$t('header.login[1]')}}</h1>
                <div class="name">
                    <div class="left">
                        <img src="../assets/img/shangcheng/name.png" alt="">
                    </div>
                    <div class="right">
                        <input type="text" :placeholder="$t('header.login[2]')" v-model="loginForm.account">
                    </div>
                </div>
                <div class="pwd">
                    <div class="left">
                        <img src="../assets/img/shangcheng/pwd.png" alt="">
                    </div>
                    <div class="right">
                        <input type="password" :placeholder="$t('header.login[3]')" v-model="loginForm.password">
                    </div>
                </div>
                <div class="form1">
                    <div class="left" @click="ispwd = !ispwd">
                        <!-- <div class="img">
                            <img src="../assets/img/shangcheng/jizhumima_01.png" alt="" v-if="ispwd">
                            <img src="../assets/img/shangcheng/jizhumima_02.png" alt="" v-else>
                        </div>
                         -->
                    </div>
                    <div class="right">
                        <!-- <a href="#">?</a> -->
                    </div>
                </div>
                <div class="btn">
                     <div class="denglu" @click="goDenglu()">
                        {{$t('header.login[4]')}}
                    </div>
                </div>
                <div class="sanfangTop">
                    <div></div>
                    <div>log in in other ways</div>
                    <div></div>
                </div>
                <div class="sanfan">
                    <fb-signin-button class="sanfang" :params="fbSignInParams" @success="onSignInSuccess" @error="onSignInError">
                        <div class="sanfangItem">
                            <div>
                                <img src="../assets/img/shangcheng/Facebook.png" alt="">
                            </div>
                            <div>
                                Facebook
                            </div>
                        </div>
                    </fb-signin-button>
                    <div class="sanfangItem">
                        <div>
                            <img src="../assets/img/shangcheng/Google.png" alt="">
                        </div>
                        <div>
                            <button v-google-signin-button="clientId" class="google-signin-button"> Continue with Google</button>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <el-dialog class="zhuceDialog" :visible.sync="zhuceDialog" width="430px">
            <p class="zhuceTitle"><a @click="qiehuan(2)">{{$t('header.register[0]')}}</a></p>
            <div class="zhuceForm">
                <h1>{{$t('header.register[1]')}}</h1>
                <div class="name">
                    <div class="left">
                        <img src="../assets/img/shangcheng/name.png" alt="">
                    </div>
                    <div class="right">
                        <input type="text" :placeholder="$t('header.register[2]')" v-model="zhuceForm.name">
                    </div>
                </div>
                <div class="pwd">
                    <div class="left">
                        <img src="../assets/img/shangcheng/pwd.png" alt="">
                    </div>
                    <div class="right">
                        <input type="password" :placeholder="$t('header.register[3]')" v-model="zhuceForm.password">
                    </div>
                </div>
                <div class="phone">
                    <div class="left">
                        <img src="../assets/img/shangcheng/email.png" alt="">
                    </div>
                    <div class="right">
                        <input type="text" :placeholder="$t('header.register[4]')" v-model="zhuceForm.email">
                    </div>
                </div>
                <div class="email">
                    <div class="left">
                        <img src="../assets/img/shangcheng/phone.png" alt="">
                    </div>
                    <div class="right">
                        <input type="text" :placeholder="$t('header.register[5]')" v-model="zhuceForm.phone">
                    </div>
                </div>
                <div class="btn">
                     <div class="denglu" @click="goZhuce()">
                        {{$t('header.register[6]')}}
                    </div>
                    <div class="xieyi">
                        <div class="yuan" v-if="xieyi" @click="xieyi = !xieyi">
                            <img src="../assets/img/shangcheng/wenjian_02.png" alt="">
                        </div>
                        <div class="yuan" v-else  @click="xieyi = !xieyi">
                            <img src="../assets/img/shangcheng/wenjian_01.png" alt="">
                        </div>
                        <div >{{$t('header.register[7]')}} <a @click="yonghuxieyi()">{{$t('header.zhuce[10]')}}</a> </div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <el-dialog class="zhucetishi" :visible.sync="zhucetishi" width="430px">
            <p class="zhuceTitle"><a></a></p>
            <div class="zhuceForm">
                <h1>{{$t('header.zhuce[1]')}}</h1>
                <div class="img">
                    <img src="../assets/img/shangcheng/dengluchenggong.png" alt="">
                </div>
                <div class="btn">
                     <div class="denglu" @click="qiehuan(2)">
                        {{$t('header.zhuce[9]')}}
                    </div>
                </div>
            </div>
        </el-dialog>
        <el-dialog
            title="$t('header.tip[0]')"
            :visible.sync="quitLogin"
            width="30%">
            <div class="quit">
                <span>{{$t('header.tip[1]')}}</span>
                <span class="dialog-footer">
                    <el-button @click="dialogVisible = false">{{$t('header.tip[2]')}}</el-button>
                    <el-button type="primary" @click="quit()">{{$t('header.tip[3]')}}</el-button>
                </span>
            </div>
        </el-dialog>
        <el-dialog
            title=""
            :visible.sync="xieyiLoading"
            width="30%">
            <div class="quit">
                <div v-html="xieyiText.content"></div>
                <span class="dialog-footer">
                    <!-- <el-button @click="dialogVisible = false">{{$t('header.tip[2]')}}</el-button> -->
                    <el-button type="primary" @click="xieyiLoading = false">{{$t('header.tip[3]')}}</el-button>
                </span>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {WOW} from 'wowjs'
import GoogleSignInButton from 'vue-google-signin-button-directive'
import jsonwebtoken from 'jsonwebtoken'
// import hello from 'hello.js/hello.js'
   export default {
        directives: {
            GoogleSignInButton
        },
        data() {
            return {
                clientId: '345345-petbt7osm0gs9mtivclevt6cjb9la43b.apps.googleusercontent.com',
                fbSignInParams: {
                    scope: 'email,user_likes',
                    return_scopes: true
                },
                xieyiLoading:false,//
                xieyiText:'',//text
                config:'',//
                login:false,
                loginDialog:false,
                zhuceDialog:false,
                zhucetishi:false,
                quitLogin:false,
                nav:[false,false,false,false,false,false,],
                navA:['home','zizhi','chanpin','guihua','haiguan','lianxi',],
                token:false,
                options: [{
                    value: 'cn',
                    label: ''
                }, {
                    value: 'en',
                    label: 'English'
                }],
                value: '',//
                topBackgroun:false,
                ispwd:false,
                xieyi:false,
                zhuceForm:{
                    name:'',
                    email:'',
                    phone:'',
                    password:'',
                },//
                loginForm:{
                    account:'',
                    password:'',
                },
                userInfo:'',
            };
        },
        created:function(){
            
        },
        mounted(){
            var _this = this;
            if(sessionStorage.userInfo){
                _this.login = true;
                _this.userInfo = JSON.parse(sessionStorage.userInfo)
            }else{
                _this.login = false;
            }
            _this.tab(0)
            var wow = new WOW({
                boxClass: 'wow',
                animateClass: 'animated',
                offset: 0,
                mobile: true,
                live: true
            })
            wow.init();
            _this.apis.get(_this.apis.config,{params:{lang:_this.apis.lang}}).then(function(res) {
                console.log(res)
                if(res.data.code == 0 ){
                    _this.config = res.data.data
                }
            })
            // hello.init({
            //     'facebook':377860950301177
            // },{
            //     redirect_uri:'./redirect'
            // })
        },  
        watch:{
            value(n,o){
                var _this = this;
                sessionStorage.lang = n
                _this.$router.go(0);
            },
            $route(){
                var _this = this;
                if(_this.$route.path == '/shouye'){
                    _this.topBackgroun = false;
                }else{
                    _this.topBackgroun = true;
                }
            },
        },
        methods:{
            OnGoogleAuthSuccess (idToken) {
                console.log(idToken,"tokesdasdasd") // token jsonwebtoken 
                // Receive the idToken and make your magic with the backend
            },
            OnGoogleAuthFail (error) {
                console.log(error)
            },

            onSignInSuccess (response) {
                // FB.api('/me', dude => {
                //   console.log(`Good to see you, ${dude.name}.`)
                // })
                console.log(response) // tolen
            },
            onSignInError (error) {
            },
            disanfang(network){
            //    hello(network).login().then(function() {
            //         alert('You are signed in to Facebook');
            //     }, function(e) {
            //         alert('Signin error: ' + e.error.message);
            //     });
            },
            shouye(){
                var _this = this;
                _this.$router.push({
                    path:'shouye'
                })
            },
            tab(index){
                var _this = this;
                for(var i=0;i<_this.nav.length;i++){
                    if(i == index){
                        _this.nav[i] = true;
                        if(_this.$route.name == _this.navA[i]){
                            return;
                        }
                        this.$router.push({name:_this.navA[i]});
                    }else{
                        _this.nav[i] = false;
                    }
                }
                _this.$forceUpdate();
            },
            yuyan(){
            },
            qiehuan(i){
                var _this = this;
                if(i==1){
                    _this.zhucetishi = false;
                    _this.zhuceDialog = true;
                }else if(i==2){
                    _this.zhuceDialog = false;
                    _this.zhucetishi = false;
                    _this.loginDialog = true;  
                }
            },
            goDenglu(){
                var _this = this;
                if(!_this.loginForm.account || !_this.loginForm.password){
                    _this.$message({
                        message: _this.$t('header.tip[4]'),
                        type: 'warning'
                    });
                    return;
                }
                
                _this.zhuceForm.lang = _this.apis.lang
                _this.apis.post(_this.apis.login,_this.loginForm).then(function(res) {
                    if(res.data.code == 0){
                        _this.loginDialog = false
                        sessionStorage.token = res.data.data.token;
                        _this.token = true;
                        _this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                        _this.$router.push({
                            path:'shouye'
                        })
                        _this.apis.get(_this.apis.get_user_info,{params:{lang:_this.apis.lang}}).then(function(res) {
                            if(res.data.code == 0){
                                console.log(res.data.data)
                                sessionStorage.userInfo = JSON.stringify(res.data.data)
                                _this.login = true
                                _this.userInfo = res.data.data
                            }else{
                                _this.login = false
                            }
                        })
                    }else{
                        _this.$message({
                            message: res.data.msg,
                            type: 'warning'
                        });
                    }
                })
            },
            goZhuce(){
                var _this = this;
                if(!_this.zhuceForm.name || !_this.zhuceForm.email || !_this.zhuceForm.phone || !_this.zhuceForm.password){
                    _this.$message({
                        message: _this.$t('header.tip[4]'),
                        type: 'warning'
                    });
                    return;
                }
                if(_this.xieyi){
                    _this.zhuceForm.lang = _this.apis.lang
                    _this.apis.post(_this.apis.register,_this.zhuceForm).then(function(res) {
                        if(res.data.code == 0){
                            _this.zhuceDialog = false;
                            _this.loginDialog = false;
                            _this.zhucetishi = true;
                        }else{
                            _this.$message({
                                message: res.data.msg,
                                type: 'warning'
                            });
                        }
                    })
                }else{
                    _this.$message({
                        message: _this.$t('header.tip[5]'),
                        type: 'warning'
                    });
                }
            },
            userClick(i){
                var _this = this;
                if(i == 1){
                    _this.loginDialog = true;
                }else if(i == 2){
                    _this.zhuceDialog = true;  
                }
            },
            //
            quit(){
                var _this = this;
                this.$confirm(_this.$t('header.tip[6]'), _this.$t('header.tip[0]'), {
                    confirmButtonText: _this.$t('header.tip[2]'),
                    cancelButtonText: _this.$t('header.tip[3]'),
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'info',
                        message: _this.$t('header.tip[7]')
                    });
                    _this.login = false;
                    _this,this.userInfo = ''
                    sessionStorage.clear();
                    _this.quitLogin = false;
                    _this.$router.push({
                        path:'shouye'
                    })
                }).catch(() => {
                    
                });
            },
            yonghuxieyi(){
                var _this = this;
                _this.xieyiLoading = true;
                _this.apis.get(_this.apis.xieyi,{params:{lang:_this.apis.lang}}).then(function(res) {
                    if(res.data.code == 0 ){
                        _this.xieyiText = res.data.data;
                    }
                })
            },
            //
            gowuche(){
                var _this = this;
                if(sessionStorage.token){
                    _this.$router.push({
                        path:'gouwuche',
                        query:{title: _this.$t('header.nav[9]')}
                    })
                }else{
                    _this.loginDialog = true
                }
            },
            gorouter(url){
                var _this = this;
                window.location.href = url
            }
        },
   }
</script>

<style lang="less">
    @import "./header.less";
</style>
<style>
</style>


